.App {
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: left;
  gap: 30px;
  background: #ffffff;
  margin: 40px 2px 0px 0px;
  justify-content: flex-start;
}

@media (min-width: 1200px) {
  .App {
    gap: 40px;
    align-items: center;
    justify-content: center;
  }
}
